import React, { useContext, useEffect, useState } from "react";
import { ShopContext } from "../../Context/ShopContext";
import Item from "../../Components/Item/Item";
import "./FilterItems.css";
import { motion } from "framer-motion";

const FilteredItems = ({ filterType, filterValue, title }) => {
  const { Datas } = useContext(ShopContext);
  const [filteredData, setFilteredData] = useState([]);

  useEffect(() => {
    let filteredItems = [];
    switch (filterType) {
      case "category":
        filteredItems = Datas.filter((data) => data.category === filterValue);
        break;
      case "discount":
        filteredItems = Datas.filter((data) => data.discount === filterValue);
        break;
      case "type":
        filteredItems = Datas.filter((data) => data.type === filterValue);
        break;
      case "newProduct":
        filteredItems = Datas.filter((data) => data.newProduct === filterValue);
        break;
      default:
        filteredItems = Datas;
        break;
    }
    setFilteredData(filteredItems);
  }, [Datas, filterType, filterValue]);

  return (
    <div className="flex flex-col pt-24 items-center relative">
      <div>
        <h1 className=" font-sb underline sticky top-[400px] h-full">
          {title} Products
        </h1>
      </div>

      <motion.div
        initial={{ opacity: 0 }}
        animate={{ opacity: 1 }}
        transition={{ duration: 1 }}
        className="grid items-center Parent-filter py-10 lg:grid-cols-4 md:grid-cols-3 xsm:grid-cols-2"
        key={filterValue} // Add a key to ensure the component is re-mounted for each filter value change
      >
        {filteredData.map((data, i) => (
          <Item
            key={i}
            id={data.id}
            title={data.title}
            old_price={data.old_price}
            new_price={data.new_price}
            image={data.image}
            image2={data.image2}
          />
        ))}
      </motion.div>
    </div>
  );
};

export default FilteredItems;
